<template>
    <div>
        <slot name="topText"></slot>    
        <div :class="['progress-bar', backgroundClass]" :style="background">
            <div :class="['progress-bar-body', frontClass]" :style="complatedStyle">
                <span><slot name="middleText"></slot></span>
            </div>
        </div>
        <slot name="bottomText"></slot>
    </div>
</template>
<script>
    export default {
        props: {
            progressStyle: {
                type: Object,
                default: () => {
                    return {}
                }
            },
            backgroundStyle: {
                type: Object,
                default: () => {
                    return {}
                }
            },
            frontClass: {
                type: String
            },
            backgroundClass: {
                type: String
            },
            progressBarWidth: {
                type: String
            }
        },
        computed: {
            complatedStyle() {
                let defaultStyle = {
                    height: '20px',
                    borderRadius: '10px',
                    backgroundColor: 'rgb(0, 204, 0)'
                }
                return Object.assign(defaultStyle, this.progressStyle, {width: this.progressBarWidth})
            },
            background() {
                let defaultStyle = {
                    width: '160px',
                    backgroundColor: 'rgb(102, 102, 102)',
                    borderRadius: '10px',
                    height: '20px',
                    overflow: 'hidden'
                }
                return Object.assign(defaultStyle, this.backgroundStyle)
            }
        }
    }
</script>
<style scoped>
    .progress-bar-body span{
        margin-left: 10px;
    }
</style>


