<template>
    <div class="h-full netdisk-panel">
        <div class="netdisk-panel-header">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <a @click="goBackByIndex({
                    path: '/',
                    name: title
                })"><span>{{title}}</span></a>
                </el-breadcrumb-item>
                <el-breadcrumb-item v-for="item in breadCrumbData" :key="item.name">
                    <a @click="goBackByIndex(item)"><span>{{item.name}}</span></a>
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="netdisk-panel-content">
            <div class="netdisk-panel-handler">
                <!-- <button class="netdisk-button-transform">{{$t('operate.upload')}}</button> -->
                <!-- 上传 -->
                <el-upload action="/athena/netdisk/uploadFile"
                    :limit="3"
                    multiple
                    :file-List="form.fileList"
                    ref="upload"
                    :show-file-list="false"
                    :on-success="uploadSuccess"
                    :before-upload="uploadTip"
                    class="pull-left m-r-xs"
                    v-if="mode !== 'myShare' && mode !== 'shareToMe'">
                   <button class="netdisk-button-transform">{{$t('operate.upload')}}</button>
                </el-upload>
                <button slot="trigger" class="netdisk-button-default m-r-xs" @click="newFolder" v-if="mode === 'files'">
                    <i class="fa fa-plus"></i>&nbsp;{{$t('operate.create')}}
                </button>
                <button class="netdisk-button-default m-r-xs" @click="downloadItems" v-if="mode !== 'myShare' && mode !== 'shareToMe'">{{$t('operate.download')}}</button>
                <el-dropdown trigger="click" v-if="mode !== 'myShare' && mode !== 'shareToMe'">
                    <button class="el-dropdown-link netdisk-button-default m-r-xs">
                        {{$t('operate.moreOperate')}}
                        <span><i class="fa fa-angle-down"></i></span>
                    </button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item v-for="item in moreOperateFields" :key="item.name">
                            <a @click="item.click"><i :class="item.iconClass"></i> {{item.name}}</a>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
                <button class="netdisk-button-sm" @click="refresh">
                    <i class="fa fa-refresh"></i>
                </button>
                <table-search
                    @search="changeSearchText"
                    :searchText="searchText"
                    class="netdisk-search"
                    ></table-search>
            </div>
            <!-- <div class="netdisk-panel-list clearfix" v-if="mode === 'picture'">
                <div v-for="image in imageList" class="netdisk-image pull-left m-r-xxl">
                    <div class="netdisk-image-body">
                        <img :src="image.src" :alt="$t('netdisk.img')">
                        <img src="@/assets/img/athena.jpg" :alt="$t('netdisk.img')">
                    </div>
                    <div class="netdisk-image-name m-t-sm">{{image.name}}</div>
                </div>
            </div> -->
            <div class="netdisk-panel-list clearfix" v-if="mode === 'myShare'">
                <netdisk-table
                    :tableData="showList"
                    :fields="filesList.fields"
                    :footerData="filesList.footerData"
                    @selection-change="selectionChange"
                    :operators="filesList.operators"
                    >
                </netdisk-table>
            </div>
            <div class="netdisk-panel-list" v-else>
                <netdisk-table
                    @getChildrenFiles="getChildrenFiles"
                    :tableData="showList"
                    :fields="filesList.fields"
                    :footerData="filesList.footerData"
                    @selection-change="selectionChange"
                    :operators="filesList.operators"
                    >
                </netdisk-table>
            </div>
        </div>
        <!-- 复制弹窗 -->
        <tip-dialog
            key="copyTo"
            useTemplate="true"
            :visible="copyToData.visible"
            dialogClass="copy-dialog"
            :title="$t('operate.copyTo')"
            @close="copyToData.close"
            @confirm="copyToData.confirm"
            >
            <template>
                <div slot="body">
                    <div class="selected-files">
                        <span>{{$t('netdisk.selectedFiles',{value: selectRows.length})}}</span>
                        <a @click="copyToData.toggleCopyToInfo" class="netdisk-link">
                            <span>{{$t('operate.info')}}</span>
                            <i class="fa fa-angle-up" v-if="copyToData.isShowInfo"></i>
                            <i class="fa fa-angle-down" v-else></i>
                        </a><br>
                        <div :class="['h-none', copyToData.isShowInfo?'is-active':'', copyToData.isFirst?'':'selected-area']">
                            <div class="pull-left m-r-xs" :key="file.id" v-for="file in selectRows">
                                {{file.name}}
                            </div>
                        </div>
                        <span class="m-t inline-block">{{$t('netdisk.selectFolder')}}</span>
                        <!-- <a class="netdisk-link" @mouseup="copyToData.newFolder">{{$t('netdisk.newFolder')}}</a> -->
                        <div class="folder-list">
                            <folder-list
                                :data="folderData.list"
                                @getSelectedFolder="folderData.methods.getSelectedFolder"
                                @createFolder="folderData.methods.createFolder">
                            </folder-list>
                        </div>
                    </div>
                </div>
            </template>        
        </tip-dialog>
        <!-- 移动弹窗 -->
        <tip-dialog
            key="moveTo"
            useTemplate="true"
            :visible="moveToData.visible"
            dialogClass="copy-dialog"
            :title="$t('operate.moveTo')"
            @close="moveToData.close"
            @confirm="moveToData.confirm"
            >
            <template>
                <div slot="body">
                    <div class="selected-files">
                        <span>{{$t('netdisk.selectedFiles',{value: selectRows.length})}}</span>
                        <a @click="moveToData.toggleMoveToInfo" class="netdisk-link">
                            <span>{{$t('operate.info')}}</span>
                            <i class="fa fa-angle-up" v-if="moveToData.isShowInfo"></i>
                            <i class="fa fa-angle-down" v-else></i>
                        </a><br>
                        <div :class="['h-none', moveToData.isShowInfo?'is-active':'', moveToData.isFirst?'':'selected-area']">
                            <div class="pull-left" :key="file.id" v-for="(file,index) in selectRows">
                                <span v-if="index !== 0" class="m-r-xs">,</span>{{file.name}}
                            </div>
                        </div>
                        <span class="m-t inline-block">{{$t('netdisk.selectFolder')}}</span>
                        <div class="folder-list">
                            <folder-list
                                :data="folderData.list"
                                @getSelectedFolder="folderData.methods.getSelectedFolder"
                                @createFolder="folderData.methods.createFolder">
                            </folder-list>
                        </div>
                    </div>
                </div>
            </template>        
        </tip-dialog>
        <!-- 分享弹窗 -->
        <tip-dialog
            key="shareTo"
            useTemplate="true"
            :visible="shareToData.visible"
            :title="$t('operate.shareTo')"
            dialogClass="share-dialog"
            @confirm="shareToData.confirm"
            @close="shareToData.close">
            <template>
                <div slot="body">
                    <div class="pull-left m-l">
                        <span>{{$t('operate.select')}}</span>
                        <div class="share-select m-t-sm">
                            <el-tree
                                @check="shareToData.checkUsers"
                                show-checkbox
                                :data="shareToData.list"
                                :props="shareToData.defaultProps"></el-tree>
                        </div>
                    </div>
                    <div class="share-middle pull-left">
                        <span @click="shareToData.pullRight"><i class="fa fa-angle-right"></i></span>
                        <span class="m-t-sm" @click="shareToData.pullLeft"><i class="fa fa-angle-left"></i></span>
                    </div>
                    <div class="pull-left">
                        <span>{{$t('operate.selected')}}</span>
                        <div class="share-select m-t-sm p-l">
                            <div v-if="shareToData.selectedDisplayList.length> 0">
                                <el-checkbox 
                                    :indeterminate="shareToData.isIndeterminate"
                                    v-model="shareToData.shareSelectAll"
                                    @change="shareToData.toggleSelectAll">
                                    {{$t('operate.selectAll')}}
                                </el-checkbox>
                            </div>
                            <div v-for="item in shareToData.selectedDisplayList" :key="item.name">
                                <el-checkbox :key="item.name" :checked="item.isSelected" @change="shareToData.toggleSelect(item)">{{item.name}}</el-checkbox>
                            </div>
                        </div>
                    </div>
                </div>
            </template>
        </tip-dialog>
        <!-- 批量删除-->
        <tip-dialog
            key="deleteItems"
            userTemplate="true"
            :visible="deleteItemsData.visible"
            :title="$t('operate.delete')"
            @close="deleteItemsData.close"
            @confirm="deleteItemsData.confirm"
            dialogClass="copy-dialog">
            <template>
                <div slot="body">
                    <div class="selected-files">
                        <span>{{$t('netdisk.selectedFiles',{value: selectRows.length})}}</span>
                        <a @click="deleteItemsData.toggleDeleteItemsInfo" class="netdisk-link">
                            <span>{{$t('operate.info')}}</span>
                            <i class="fa fa-angle-up" v-if="deleteItemsData.isShowInfo"></i>
                            <i class="fa fa-angle-down" v-else></i>
                        </a><br>
                        <div :class="['h-none', deleteItemsData.isShowInfo?'is-active':'', deleteItemsData.isFirst?'':'selected-area']">
                            <div class="pull-left m-r-xs" :key="file.id" v-for="file in selectRows">
                                {{file.name}}
                            </div>
                        </div>
                        <span class="m-t inline-block">{{$t('netdisk.deleteItems')}}</span>
                    </div>
                </div>
            </template>
        </tip-dialog>
        <!-- 分享详情 -->
        <tip-dialog
            key="shareInfo"
            type="tip"
            userTemplate="true"
            :visible="shareInfoData.visible"
            :title="$t('operate.shareInfo')"
            @close="shareInfoData.confirm"
            dialogClass="info-dialog">
            <template>
                <div slot="body">
                    <div class="m-l-md m-r-xl">
                        <button class="netdisk-button-transform m-r-xs padder" @click="shareInfoData.addSharedUser">{{$t('operate.newSharedUser')}}</button>
                        <button class="netdisk-button-default m-r-xs padder" @click="shareInfoData.cancelSelectedFiles">{{$t('operate.shareCancel')}}</button>
                        <button class="netdisk-button-default m-r-xs padder"  @click="shareInfoData.cancelAllFiles">{{$t('operate.shareAllCancel')}}</button>
                        <table-search
                            @search="shareInfoData.changeSearchText"
                            :searchText="shareInfoData.searchText"
                            class="netdisk-search"
                            ></table-search>
                        <div class="m-t-md share-info-body">
                            <el-table
                                stripe
                                height="280"
                                :header-cell-style="{backgroundColor: '#f5f7f8'}"
                                :data="shareInfoData.showList"
                                @selection-change="shareInfoData.selectionChange"
                                >
                                <el-table-column type="selection"></el-table-column>
                                <el-table-column v-for="item in shareInfoData.fields" :key="item.name" :label="item.showName" :width="item.width">    
                                    <template slot-scope="scope">
                                        <span>{{item.format?item.format(scope.row[item.name]):scope.row[item.name]}}</span>
                                    </template>
                                </el-table-column>
                                <el-table-column key="operation" width="80" :label="$t('netdisk.operation')">
                                    <template slot-scope="scope">
                                        <div class="netdisk-list-operation">
                                            <a @click="shareInfoData.shareItemCancel(scope.row)">
                                                <span :title="$t('operate.shareCancel')">{{$t('operate.shareCancel')}}</span>
                                            </a>
                                        </div>
                                    </template>
                                </el-table-column>
                            </el-table>   
                        </div>     
                    </div>
                </div>
            </template>
        </tip-dialog>
        <tip-dialog
            key="delete"
            userTemplate="true"
            :visible="deleteVisible"
            :title="$t('operate.delete')"
            @close="deleteClose"
            @confirm="deleteConfirm"
            dialogClass="waring-tip-dialog">
            <template>
                <div slot="body">
                    <div class="pull-left">
                        <i class="fa fa-exclamation-circle"></i>
                    </div>
                    <div>
                        <span>{{$t("operate.delete")}}</span>
                        <span class="text-info">{{deleteData.name}}</span>
                        <span>{{$t('netdisk.deleteMessage')}}</span>
                    </div>
                </div>
            </template>
        </tip-dialog>
        <edit-dialog
            :visible="newFolderForm.visible"
            :data="newFolderForm.data"
            :title="newFolderForm.title"
            :fields="newFolderForm.fields"
            @close="newFolderForm.close"
            @confirm="newFolderForm.confirm"
            ></edit-dialog>
    </div>
</template>
<script>
    import netdiskTable from './netdisk-table'
    import tableSearch from '../table/table-search'
    import {dateFormat, isArray, isFunction,upperCaseFirst} from '@/assets/js/untils'
    import tipDialog from '../common/tipDialog'
    import folderList from '../common/folderList'
    import editDialog from '../common/editDialog'

    const FILE_TYPE = {
        1: "document",    //文件
        2: "music",       //音乐
        3: "video",       //视频
        4: "picture",     //图片
        5: "compress"     //压缩包
    }
    
    const customToString = val => {
        if (typeof val === 'string') {
            return val
        }
        if (typeof val === 'number') {
        // todo: 大数字怎么办
        return val + ''
        }
        return ''
    }

    const search = (fields = []) => (searchText, data) => {
        searchText = searchText.trim()
        if (searchText === '') {
            return data
        }
        return data.filter(item => {
            if (!fields.length) {
                let atts = []
                for (let att in item) {
                    if (att !== '$$src') {
                        atts.push(att)
                    }
                }
                fields = atts
            }
            return fields.some(field => ~customToString(item[field]).indexOf(searchText))
        })
    }

    const initNewFolderList = list => {
        list.forEach(item => {
            if(item.type === 0) {
                item.newFolders = []
                item.isSelect = false
                item.isOpen = false
                if(item.folder) {
                    initNewFolderList(item.folder)
                }
            }
        })
        return list
    }

    const getSelectedFolder = (list, path) => {
        list.forEach(item => {
            if(item.type === 0) {
                item.isSelect = item.path === path
                if (item.shares) {
                    getSelectedFolder(item.folder, path)
                }
            }
        })
        return list
    }

    const getSelectedObj = (list, path) => {
        let target
        list.some(item => {
            if(item.path === path) {
                target = item
                return true
            } else {
                if(item.folder) {
                    target = getSelectedObj(item.folder, path)
                }
                
            }
        })
        return target
    }

    export default {
        created() {
            this.getFilesList(this.basePath)
            this.search = this.initSearch()
            this.shareToData.getCourseGroup()
        },
        data() {
            return {
                form: {
                    fileList: []
                },
                breadCrumbData: [],
                isUprloadProgress: false,
                selectRows: [],
                basePath: '/',
                imageList: [],
                deleteVisible: false,
                deleteData: {},
                moreOperateFields: [{
                    name: this.$t('operate.copyTo'),
                    iconClass: 'fa fa-copy',
                    click: this.copyTo
                }, {
                    name: this.$t('operate.moveTo'),
                    iconClass: 'fa fa-arrow-circle-o-right',
                    click: this.moveTo
                }, {
                    name: this.$t('operate.shareTo'),
                    iconClass: 'fa fa-share-alt',
                    click: this.shareTo
                }, {
                    name: this.$t('operate.delete'),
                    iconClass: 'fa fa-trash',
                    click: this.deleteItems
                }],
                filesList: {
                    data: [],
                    fields: [{
                        name: 'contentLength',
                        showName: this.$t('netdisk.size'),
                        width: '120'
                    }, {
                        name: 'modified',
                        showName: this.$t('netdisk.editTime'),
                        width: '120'
                    }],
                    footerData: {
                        already: 0,
                        all: 0
                    },
                    operators: [{
                        name: 'share',
                        iconClass: 'fa fa-share-alt',
                        handler: this.share
                    }, {
                        name: 'download',
                        iconClass: 'fa fa-long-arrow-down',
                        handler: this.download
                    }, {
                        name: 'shareInfo',
                        iconClass: 'fa fa-list',
                        handler: this.shareInfo
                    }, {
                        name: 'delete',
                        iconClass: 'fa fa-trash',
                        handler: this.delete
                    }]
                },
                searchText: '',
                search: (t, d) => d,
                showList: [],
                copyToData: {
                    visible: false,
                    isShowInfo: false,
                    isFirst: true,
                    toggleCopyToInfo: () => {
                        let visible = this.copyToData.isShowInfo
                        this.copyToData.isShowInfo = !visible
                        this.copyToData.isFirst = false
                    },
                    confirm: () => {
                        let pathObj = {
                            destPath: '',
                            srcPath: []
                        }
                        pathObj.destPath = this.folderData.selectedPath
                        this.selectRows.forEach(item => {
                            pathObj.srcPath.push(item.path)
                        })
                        this.$httpMethods.netDisk.copyFiles(pathObj).then(res => {
                            this[this.getQueryType(this.mode)](this.basePath)
                            this.copyToData.visible = false
                            this.copyToData.isFirst = true
                            this.$notify({
                                type: 'success',
                                title: this.$t('common.success'),
                                message: this.$t('netdisk.copySuccess')
                            })
                        })
                    },
                    close: () => {
                        this.copyToData.visible = false
                        this.copyToData.isFirst = true
                    }
                    // newFolder: () => {
                    //     let targetFolder = getSelectedObj(this.folderData.list, this.folderData.selectedPath)
                    //     targetFolder.newFolders.push({
                    //         value: this.$t('operate.create'),
                    //         isEdit: true
                    //     })
                    //     targetFolder.isOpen = true
                    // }
                },
                moveToData: {
                    visible: false,
                    isShowInfo: false,
                    isFirst: true,
                    toggleMoveToInfo: () => {
                        let visible = this.moveToData.isShowInfo
                        this.moveToData.isShowInfo = !visible
                        this.moveToData.isFirst = false
                    },
                    confirm: () => {
                        let pathObj = {
                            destPath: '',
                            srcPath: []
                        }
                        pathObj.destPath = this.folderData.selectedPath
                        this.selectRows.forEach(item => {
                            pathObj.srcPath.push(item.path)
                        })
                        this.$httpMethods.netDisk.moveFile(pathObj).then(res => {
                            this[this.getQueryType(this.mode)](this.basePath)
                            this.moveToData.visible = false
                            this.moveToData.isFirst = true
                            this.$notify({
                                type: 'success',
                                title: this.$t('common.success'),
                                message: this.$t('netdisk.moveSuccess')
                            })
                        })
                    },
                    close: () => {
                        this.moveToData.visible = false
                        this.moveToData.isFirst = true
                    }
                },
                shareToData: {
                    visible: false,
                    list: [],
                    selectedList: [],
                    selectedDisplayList: [],
                    shareSelectAll: false,
                    isIndeterminate: false,
                    isItem: false,
                    submitObj: {
                        path: [],
                        shareWith: []
                    },
                    defaultProps: {
                        label: 'name',
                        children: 'users'
                    },
                    close: () => {
                        this.shareToData.visible = false
                    },
                    confirm: () => {
                        this.shareToData.selectedDisplayList.forEach(item => {
                            this.shareToData.submitObj.shareWith.push(item.username)
                        })
                        this.selectRows.forEach(item => {
                            this.shareToData.submitObj.path.push(item.path)
                        })
                        this.$httpMethods.netDisk.shareFile(this.shareToData.submitObj).then(res => {
                            this[this.getQueryType(this.mode)](this.basePath)
                            this.shareToData.visible = false
                            this.shareToData.resetData()
                        })
                    },
                    resetData: () => {
                        this.shareToData.submitObj = {
                            path: [],
                            shareWith: []
                        }
                    },
                    getCourseGroup: () => {
                        this.$httpMethods.lessonGroup.queryLessonGroupAndUsers().then(res=> {
                            this.shareToData.list = res.data
                        })
                    },
                    checkUsers: (obj, selectedObj) => {
                        this.shareToData.selectedList = selectedObj.checkedNodes.filter(node => {
                            return !node.users
                        })
                    },
                    pullRight: () => {
                        let arr = this.shareToData.selectedDisplayList
                        this.shareToData.selectedDisplayList = this.shareToData.selectedList
                        this.shareToData.selectedDisplayList.forEach(item => {
                            if(arr.indexOf(item)<0){
                                item.isSelected = false
                            }
                        })
                        this.shareToData.toggleSelect()
                    },
                    pullLeft: () => {
                        this.shareToData.selectedDisplayList = this.shareToData.selectedDisplayList.filter(item => {
                            return !item.isSelected
                        })
                        this.shareToData.toggleSelect()
                    },
                    toggleSelectAll: value => {
                        this.shareToData.selectedDisplayList.forEach(item => {
                            item.isSelected = value
                        })
                        this.shareToData.isIndeterminate = false
                    },
                    toggleSelect: value => {
                        if(value) {
                            let checked = value
                            value.isSelected = !checked
                        }
                        let isNone = true
                        let isAll = true
                        this.shareToData.selectedDisplayList.forEach(item => {
                            if(!item.isSelected) {
                                isAll = false
                            } else {
                                isNone = false
                            }
                        })
                        this.shareToData.shareSelectAll = isAll
                        this.shareToData.isIndeterminate = !isAll && !isNone
                    }
                },
                deleteItemsData: {
                    visible: false,
                    isShowInfo: false,
                    isFirst: true,
                    toggleDeleteItemsInfo: () => {
                        let visible = this.deleteItemsData.isShowInfo
                        this.deleteItemsData.isShowInfo = !visible
                        this.deleteItemsData.isFirst = false
                    },
                    close: () => {
                        this.deleteItemsData.visible = false
                        this.deleteItemsData.isFirst = true
                    },
                    confirm: () => {
                        let paths = []
                        this.selectRows.forEach(item => {
                            paths.push(item.path)
                        })
                        this.$httpMethods.netDisk.removeFiles(paths).then(res => {
                            this.deleteItemsData.visible = false
                            this.deleteItemsData.isFirst = true
                            this.$notify({
                                type: 'success',
                                title: this.$t('common.success'),
                                message: this.$t('netdisk.deleteSuccess')
                            })
                            this[this.getQueryType(this.mode)](this.basePath)
                        })
                    }
                },
                shareInfoData: {
                    visible: false,
                    searchText: '',
                    selectRows: [],
                    list: [],
                    filePath: '',
                    fields: [{
                        name: 'shareWithDisplayName',
                        showName: this.$t('netdisk.hwName')
                    }, {
                        name: 'shareWithGroupInfo',
                        showName: this.$t('operate.shareGroup'),
                        width: 200
                    }, {
                        name: 'stime',
                        showName: this.$t('operate.shareTime'),
                        width: 160,
                        format: time => {
                            return dateFormat(time, 'yyyy-MM-dd HH:mm:ss')
                        }
                    }],
                    confirm: () => {
                        this.shareInfoData.visible = false
                        this.shareInfoData.isFirst = true
                    },
                    selectionChange: selectRows => {
                        this.shareInfoData.selectRows = selectRows
                    },
                    changeSearchText: newVal => {
                        this.shareInfoData.searchText = newVal
                        this.shareInfoData.showList = search(['shareWithDisplayName'])(this.shareInfoData.searchText, this.shareInfoData.list)
                    },
                    getShareInfo: obj => {
                        this.shareInfoData.filePath = obj.path
                        this.shareInfoData.list = obj.shares
                        this.shareInfoData.showList = this.shareInfoData.list
                    },
                    shareItemCancel: obj => {
                        this.$httpMethods.netDisk.cancelShares(obj.id).then(res => {
                            if(res && res.status === 200) {
                                this.getSharedInfoFiles(this.shareInfoData.filePath)
                            }
                            
                        })
                    },
                    addSharedUser: () => {
                        this.shareToData.submitObj.path.push(this.shareInfoData.filePath)
                        this.shareToData.visible = true
                        this.shareToData.isItem = true
                    },
                    cancelSelectedFiles: () => {
                        let ids = []
                        this.shareInfoData.selectRows.forEach(row => {
                            ids.push(row.id)
                        })
                        this.$httpMethods.netDisk.cancelShares(ids).then(res => {
                            if(res && res.status === 200) {
                                this.getSharedInfoFiles(this.shareInfoData.filePath)
                            }   
                        })
                    },
                    cancelAllFiles: () => {
                        let ids = []
                        this.shareInfoData.list.forEach(row => {
                            ids.push(row.id)
                        })
                        this.$httpMethods.netDisk.cancelShares(ids).then(res => {
                            if(res && res.status === 200) {
                                this.getSharedInfoFiles(this.shareInfoData.filePath)
                            }
                        })
                    }
                },
                folderData: {
                    list: [],
                    selectedPath: '',
                    methods: {
                        getSelectedFolder: (path) => {
                            this.folderData.list = getSelectedFolder(this.folderData.list, path)
                            this.folderData.selectedPath = path
                        },
                        getFolderList: () => {
                            this.$httpMethods.netDisk.queryAllFiles().then(res => {
                                this.folderData.list = initNewFolderList(res.data)
                            })
                        },
                        createFolder: () => {
                            let path = this.folderData.selectedPath
                            let list = []
                            this.folderData.methods.getFolderList()
                        }
                    }
                },
                newFolderForm: {
                    visible: false,
                    title: this.$t('netdisk.newFolder'),
                    data: {
                        folderName: ''
                    },
                    fields: [{
                        name: 'folderName',
                        showName: this.$t('netdisk.folderName'),
                        type: 'text',
                        required: true
                    }],
                    close: () => {
                        this.newFolderForm.visible = false
                        this.newFolderForm.data = {
                            folderName: ''
                        }
                    },
                    confirm: (data) => {
                        let path = this.basePath + data.folderName +'/'
                        this.$httpMethods.netDisk.createFolder(path).then(res => {
                            if(res && res.status === 200) {
                                this.getFilesList(this.basePath)
                                this.$notify({
                                    type: 'success',
                                    title: this.$t('common.success'),
                                    message: this.$t('netdisk.createFolderSuccess')
                                })
                                this.newFolderForm.visible = false
                                this.newFolderForm.data = {
                                    folderName: ''
                                }
                            }
                        })
                    }
                }
                
            }
        },
        props: {
            mode: {
                type: String
            },
            title: {
                type: String
            },
            searchOpt: Array || Function,
        },
        methods: {
            copyTo() {
                this.$httpMethods.netDisk.queryAllFiles().then(res => {
                    if(res && res.status === 200) {
                        this.folderData.list = initNewFolderList(res.data)
                        this.copyToData.visible = true
                    }
                })
            },
            moveTo() {
                this.$httpMethods.netDisk.queryAllFiles().then(res => {
                    if(res && res.status === 200) {
                        this.folderData.list = initNewFolderList(res.data)
                        this.moveToData.visible = true
                    }
                })
            },
            shareTo() {
                this.shareToData.visible = true
                this.shareToData.isItem = false
            },
            delete(obj) {
                this.deleteData = obj
                this.deleteVisible = true
            },
            deleteClose() {
                this.deleteVisible = false
            },
            deleteConfirm() {
                this.$httpMethods.netDisk.removeFiles(this.deleteData.path).then(res => {
                    this.deleteVisible = false
                    if(res && res.status === 200) {
                        this.$notify({
                            type: 'success',
                            title: this.$t('common.success'),
                            message: this.$t('netdisk.deleteSuccess')
                        })
                        this[this.getQueryType(this.mode)](this.basePath)
                    }
                })
            },
            deleteItems() {
                this.deleteItemsData.visible = true
            },
            share(obj) {
                this.shareToData.submitObj.path.push(obj.path)
                this.shareToData.visible = true
                this.shareToData.isItem = true
            },
            download(obj) {
                window.location.href = this.$httpMethods.netDisk.downloadFile(obj.path)
            },
            downloadItems() {
                this.selectRows.forEach(item => {
                    window.open(this.$httpMethods.netDisk.downloadFile(item.path))
                })
            },
            shareInfo(obj) {
                this.shareInfoData.visible = true
                this.shareInfoData.getShareInfo(obj)
            },
            getFilesList(path, isRefresh) {
                this.$httpMethods.netDisk.queryFilesByPath(path).then(res => {
                    if(res && res.status === 200) {
                        this.filesList.data = res.data
                        this.showList = res.data
                        this.setFooterData(5, res.data.length)
                        if(!!isRefresh) {
                            this.$notify({
                                type: 'success',
                                title: this.$t('common.success'),
                                message: this.$t('netdisk.refreshSuccess')
                            })
                        }
                    }
                })
            },
            getSharedInfoFiles(path) {
                if(this.mode === 'myShare') {
                    this.$httpMethods.netDisk.queryFilesBySharedWith('false').then(res => {
                        if(res && res.status === 200) {
                            this.filesList.data = res.data
                            this.showList = res.data
                            this.shareInfoData.list = []
                            this.shareInfoData.showList = []
                            if(this.showList.length > 0) {
                                this.showList.some(item => {
                                    if(item.path === path) {
                                        this.shareInfoData.list = item.shares
                                        this.shareInfoData.showList = this.shareInfoData.list
                                    }
                                })
                            }
                            this.$notify({
                                type: 'success',
                                title: this.$t('common.success'),
                                message: this.$t('netdisk.cancelShareSuccess')
                            })
                        }
                    })
                } else {
                    this.$httpMethods.netDisk.queryFilesByPath(this.basePath).then(res => {
                        if(res && res.status === 200) {
                            this.filesList.data = res.data
                            this.showList = res.data
                            this.shareInfoData.list = []
                            this.shareInfoData.showList = []
                            if(this.showList.length > 0) {
                                this.shareInfoData.list = []
                                this.shareInfoData.showList = []
                                this.showList.some(item => {
                                    if(item.path === path) {
                                        this.shareInfoData.list = item.shares
                                        this.shareInfoData.showList = this.shareInfoData.list
                                    }
                                })
                            }
                            this.$notify({
                                type: 'success',
                                title: this.$t('common.success'),
                                message: this.$t('netdisk.cancelShareSuccess')
                            })
                        }
                    })
                }
                
            },
            getMyShareFilesList(isCancel, id) {
                this.$httpMethods.netDisk.queryFilesBySharedWith('false').then(res => {
                    if(res && res.status === 200) {
                        this.filesList.data = res.data
                        this.showList = res.data
                        if(isCancel) {
                            this.showList.some(item => {
                                if(item.id === id) {
                                    this.shareInfoData.list = item.shares
                                    this.shareInfoData.showList = this.shareInfoData.list
                                }
                            })
                        }
                    }
                })
            },
            getDocumentList(path, isRefresh) {
                this.$httpMethods.netDisk.queryFilesByType(FILE_TYPE[1]).then(res => {
                    if(res && res.status === 200) {
                        this.filesList.data = res.data
                        this.showList = res.data
                        if(!!isRefresh) {
                            this.$notify({
                                type: 'success',
                                title: this.$t('common.success'),
                                message: this.$t('netdisk.refreshSuccess')
                            })
                        }
                    }
                })
            },
            getMusicList(path, isRefresh) {
                this.$httpMethods.netDisk.queryFilesByType(FILE_TYPE[2]).then(res => {
                    if(res && res.status === 200) {
                        this.filesList.data = res.data
                        this.showList = res.data
                        if(!!isRefresh) {
                            this.$notify({
                                type: 'success',
                                title: this.$t('common.success'),
                                message: this.$t('netdisk.refreshSuccess')
                            })
                        }
                    }
                })
            },
            getVideoList(path, isRefresh) {
                this.$httpMethods.netDisk.queryFilesByType(FILE_TYPE[3]).then(res => {
                    if(res && res.status === 200) {
                        this.filesList.data = res.data
                        this.showList = res.data
                        if(!!isRefresh) {
                            this.$notify({
                                type: 'success',
                                title: this.$t('common.success'),
                                message: this.$t('netdisk.refreshSuccess')
                            })
                        }
                    }
                })
            },
            getPictureList(path, isRefresh) {
                this.$httpMethods.netDisk.queryFilesByType(FILE_TYPE[4]).then(res => {
                    if(res && res.status === 200) {
                        this.filesList.data = res.data
                        this.showList = res.data
                        if(!!isRefresh) {
                            this.$notify({
                                type: 'success',
                                title: this.$t('common.success'),
                                message: this.$t('netdisk.refreshSuccess')
                            })
                        }
                    }
                })
            },
            getCompressList(path, isRefresh) {
                this.$httpMethods.netDisk.queryFilesByType(FILE_TYPE[5]).then(res => {
                    if(res && res.status === 200) {
                        this.filesList.data = res.data
                        this.showList = res.data
                        if(!!isRefresh) {
                            this.$notify({
                                type: 'success',
                                title: this.$t('common.success'),
                                message: this.$t('netdisk.refreshSuccess')
                            })
                        }
                    }
                })
            },
            initSearch() {
                if (isFunction(this.searchOpt)) {
                    return this.searchOpt
                }
                if (isArray(this.searchOpt)) {
                    return search(this.searchOpt)
                }
                return search()
            },
            changeSearchText(newVal) {
                this.searchText = newVal
                if(this.mode === 'picture') {

                } else {
                    this.showList = this.search(this.searchText, this.filesList.data)
                }
            },
            selectionChange(selectRows) {
                this.selectRows = selectRows
            },
            newFolder() {
                this.newFolderForm.visible = true
            },
            getQueryType(mode) {
                let modeStr = upperCaseFirst(mode)
                let methodName = ''
                if(mode === 'share') {

                } else {
                    methodName = 'get' + modeStr + 'List'
                }
                return methodName
            },
            refresh() {
                this[this.getQueryType(this.mode)](this.basePath, true)
            },
            uploadSuccess() {
                this.$notify({
                    type: 'success',
                    title: this.$t('common.success'),
                    message: this.$t('netdisk.uploadSuccess')
                })
                this[this.getQueryType(this.mode)](this.basePath)
                this.isUploadProgress = false
            },
            uploadTip() {
                if(!this.isUploadProgress) {
                    this.$notify({
                        type: 'info',
                        title: this.$t('common.tip'),
                        message: this.$t('netdisk.onUpload')
                    })
                    this.isUploadProgress = true
                }
            },
            setFooterData(already, all) {
                this.filesList.footerData.already = already
                this.filesList.footerData.all = all
            },
            getChildrenFiles(item) {
                this.breadCrumbData.push(item)
                this.getFilesList(item.path)
                this.basePath = item.path
            },
            goBackByIndex(item) {
                if(this.breadCrumbData.indexOf(item) === -1) {
                    this.$httpMethods.netDisk.queryFilesByPath(item.path).then(res => {
                        if(res && res.status === 200) {
                            this.filesList.data = res.data
                            this.showList = res.data
                            this.breadCrumbData = []
                            this.basePath = item.path
                            this.setFooterData(5, res.data.length)
                        }
                    })
                } else {
                    this.$httpMethods.netDisk.queryFilesByPath(item.path).then(res => {
                        if(res && res.status === 200) {
                            this.filesList.data = res.data
                            this.showList = res.data
                            this.breadCrumbData = this.breadCrumbData.slice(0,this.breadCrumbData.indexOf(item))
                            this.breadCrumbData.push(item)
                            this.basePath = item.path
                            this.setFooterData(5, res.data.length)
                        }
                    })
                }
            }
        },
        components: {
            netdiskTable,
            tableSearch,
            tipDialog,
            folderList,
            editDialog
        },
        watch: {
            title(newVal) {
                this.computedTitle = newVal
            },
            mode(newVal) {
                this.changeSearchText('')
                if(newVal === 'myShare') {
                    this.getMyShareFilesList()
                }
                if(newVal === 'files') {
                    this.getFilesList(this.basePath)
                    this.filesList.fields =  [{
                        name: 'contentLength',
                        showName: this.$t('netdisk.size'),
                        width: '120'
                    }, {
                        name: 'modified',
                        showName: this.$t('netdisk.editTime'),
                        width: '120'
                    }]
                    return
                }
                if(newVal === FILE_TYPE[1]) {
                    this.getDocumentList()
                }
                if(newVal === FILE_TYPE[2]) {
                    this.getMusicList()
                }
                if(newVal === FILE_TYPE[3]) {
                    this.getVideoList()
                }
                if(newVal === FILE_TYPE[4]) {
                    this.getPictureList()
                }
                if(newVal === FILE_TYPE[5]) {
                    this.getCompressList()
                }
                this.filesList.fields = []
            }
        }
    }
</script>
<i18n>
    {
        "zh": {
            "netdisk": {
                "hwName": "作业名称",
                "size": "大小",
                "img": "图片",
                "operation": "操作",
                "editTime": "修改时间",
                "shared":"已分享({value}人)",
                "selectedFiles": "您已选择{value}个文件(夹)",
                "selectFolder": "请选择目标文件",
                "newFolder": "新建文件夹",
                "folderName": "文件夹名称",
                "deleteItems": "删除所选文件(夹)将无法找回,您确定要永久删除所选文件(夹)吗?",
                "onUpload": "文件正在上传，请稍等",
                "uploadSuccess": "文件上传成功",
                "deleteMessage": "将无法找回，您确定要永久删除所选文件(夹)吗?",
                "deleteSuccess": "文件(夹)删除成功",
                "copySuccess": "文件(夹)复制成功",
                "moveSuccess": "文件(夹)移动成功",
                "refreshSuccess": "刷新列表成功",
                "createFolderSuccess": "新建文件夹成功",
                "cancelShareSuccess": "文件(夹)取消分享成功"
            },
            "operate": {
                "upload": "上传",
                "create": "新建文件夹",
                "download": "下载",
                "release": "发布作业",
                "moreOperate": "更多操作",
                "copyTo": "复制",
                "moveTo": "移动",
                "shareTo": "分享",
                "delete": "删除",
                "info": "详情",
                "select": "选择",
                "selected": "已选",
                "selectAll": "全选",
                "shareInfo": "分享详情",
                "newSharedUser": "增加分享用户",
                "shareCancel": "取消分享",
                "shareAllCancel": "取消全部分享",
                "shareGroup": "课程组",
                "shareTime": "分享时间"
            },
             "table": {
                "footer": {
                    "already": "已加载",
                    "all": "共",
                    "item": "项"
                },
                "hwName": "作业名称",
                "shared": "已分享({value}人)"
            }
        }
    }
</i18n>
<style scoped>
    .netdisk-panel {
        float: left;
        width: 85%;
        background-color: rgb(237, 237, 244);
    }
    .netdisk-panel-header {
        background-color: #fff;
        height: 60px;
        line-height: 60px;
        padding-left: 20px;
        font-size: 12px;
        box-shadow: 1px 1px 2px 0px rgba(4, 4, 4, 0.06);
        display: flex;
        align-items: center;
    }
    .netdisk-panel-header .el-breadcrumb__item:last-child .el-breadcrumb__inner a,
    .netdisk-panel-header .el-breadcrumb__item .el-breadcrumb__inner a{
        font-family: 'MicrosoftYaHei';
        font-size: 16px;
        color: #0c92fe;
        font-weight: bold;
        cursor: pointer;
    }
    .el-breadcrumb {
        font-size: 16px;
    }
    .netdisk-panel-content {
        margin: 20px 25px;
        padding: 30px;
        background-color: #fff;
    }
    .netdisk-panel-handler {
        background-color: #fefefe;
        margin-bottom: 20px;
        height: 30px;
        font-size: 0;
    }
    .netdisk-search {
        width: 360px;
        height: 30px;
        float: right;
    }
    .netdisk-button-transform {
        height: 30px;
        min-width: 80px;
        background: linear-gradient(-90deg, #0090ff 0%, #6c50fd 100%);
        border-radius: 1px;
        border: 0;
        color: #fff;
        cursor: pointer;
    }
    .netdisk-button-default {
        height: 30px;
        min-width: 80px;
        background-color: #e6f4ff;
        border-radius: 1px;
        border: 0;
        color: #22a5df;
        cursor: pointer;
    }
    .netdisk-image .netdisk-image-body{
        width: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .netdisk-image-name {
        font-size: 12px;
        color: #696e79;
        white-space: normal;
        word-break: break-all;
        word-wrap: break-word;
        width: 100px;
    }
    .netdisk-image img {
        max-height: 70px;
        max-width: 70px;
    }
</style>


