<template>
    <div class="netdisk-nav">
        <el-row class="netdisk-nav-header">
            <span>{{$t('netdisk.title')}}</span>
        </el-row>
        <el-row class="netdisk-nav-body">
            <el-col :span="24">
                <el-menu 
                    class="el-menu-netdisk"
                    :default-active="data.default" 
                    background-color="#f4f5f5" 
                    text-color="#696e79" 
                    active-text-color="#0c92fe">
                        <el-menu-item :index="item.type" @click="changeType(item)" :key="item.type" v-for="item in data.list">
                            <i :class="item.icon"></i>
                            <span slot="title">{{item.name}}</span>
                        </el-menu-item>
                </el-menu>
            </el-col>
        </el-row>
        <progress-bar 
            :progressBarWidth="progressBarWidth" 
            :backgroundStyle="progressBarStyle.background" 
            :progressStyle="progressBarStyle.front"
            class="netdisk-progress-bar">
            <div slot="bottomText">
                <span class="progress-bar-text">{{progressBarFont}}</span>
            </div>
        </progress-bar>
    </div>
</template>
<script>
    import progressBar from '../common/progressBar'
    export default {
        created() {
            this.getQuotaUse()
        },
        data() {
            return {
                mode: this.data.default,
                progressBarStyle: {
                    front: {
                        background: '#0fcb68',
                        height: '6px',
                        borderRadius: '3px'
                    },
                    background: {
                        background: '#eaeaf0',
                        height: '6px',
                        borderRadius: '3px',
                        width: '150px'
                    }
                },
                progressBarShow: {
                    relative: '0%',
                    used: '0',
                    total: '0'
                }
            }
        },
        props: {
            data: {
                type: Object,
                default: () => {
                    return {}
                }
            }
        },
        methods: {
            changeType(item) {
                this.$emit('changeType', item)
            },
            getQuotaUse() {
                this.$httpMethods.netDisk.getQuotaUse().then(res => {
                    if(res && res.status === 200) {
                        this.progressBarShow = res.data
                    }
                })
            }
        },
        components: {
            progressBar
        },
        computed: {
            progressBarWidth() {
                return this.progressBarShow.relative
            },
            progressBarFont() {
                return this.progressBarShow.used + '/' + this.progressBarShow.total
            }
        }
    }
</script>
<i18n>
    {
        "zh": {
            "netdisk": {
                "title": "个人网盘"
            }
        }
    }
</i18n>
<style scoped>
    .netdisk-nav {
        float: left;
        width: 15%;
        background: #f4f5f5;
        min-height: 100%;
        position: relative;
    }
    .netdisk-nav-header {
        color: #696e79;
        padding: 15px;
        font-size: 14px;
        background: #eaeff1;
        height: 60px;
        line-height: 30px;
        font-weight: bold;
        border-bottom: 1px solid rgba(105, 110, 121, 0.3);
    }
    .netdisk-nav-body i {
        color: #0c92fe;
    }
    .netdisk-progress-bar {
        position: absolute;
        bottom: 65px;
        left: 15px;
    }
    .el-menu-netdisk {
        border: 0;
    }

    .el-menu-netdisk .el-menu-item:hover {
        background-color: #ffffff !important;
        box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.05);
    }
    .el-menu-netdisk .el-menu-item.is-active {
        padding-left: 0 !important;
        background: #fff !important;
    }
    .el-menu-netdisk .is-active::before {
        content: "";
        display: block;
        background-color: #0c92fe;
        height: 100%;
        width: 4px;
        float: left;
        margin-right: 15px;
    }
</style>


