<template>
    <div style="width: 100%;height: 100%;">
        <netdisk-nav :data="navData" @changeType="changeType"></netdisk-nav>
        <netdisk-panel :mode="panelData.mode" :searchOpt="searchOpt" :title="panelData.title"></netdisk-panel>
    </div>
</template>
<script>
    import netdiskNav from './netdisk-nav'
    import netdiskPanel from './netdisk-panel'
    export default {
        data() {
            return {
                navData: {
                    default: 'files',
                    list: [{
                        name: this.$t('nav.files'),
                        icon: 'el-icon-menu',
                        type: 'files'
                    }, {
                        name: this.$t('nav.image'),
                        icon: 'el-icon-menu',
                        type: 'picture'
                    }, {
                        name: this.$t('nav.music'),
                        icon: 'el-icon-menu',
                        type: 'music'
                    }, {
                        name: this.$t('nav.video'),
                        icon: 'el-icon-menu',
                        type: 'video'
                    }, {
                        name: this.$t('nav.compress'),
                        icon: 'el-icon-menu',
                        type: 'compress'
                    }, {
                        name: this.$t('nav.myShare'),
                        icon: 'el-icon-menu',
                        type: 'myShare'
                    }]
                },
                panelData:{
                    mode: 'files',
                    title: this.$t('nav.files')
                },
                searchOpt: ['name']
            }
        },
        methods: {
            changeType(item) {
                this.panelData.mode = item.type
                this.panelData.title = item.name
            }
        },
        components: {
            netdiskNav,
            netdiskPanel
        }
    }
</script>
<i18n>
    {
        "zh": {
            "nav": {
                "files": "全部文件",
                "myShare": "我的分享",
                "image": "图片",
                "music": "音乐",
                "video": "视频",
                "compress": "压缩包"
            }
        }
    }
</i18n>
<style>
    .netdisk-button-sm {
        width: 30px;
        height: 30px;
        background-color: #ffffff;
        border-radius: 1px;
        border: solid 1px #cad8df;
        color: #b0bec5;
        cursor: pointer;
        float: right;
        display: inline-block;
        margin-left: 5px;
    }
    .progress-bar-text {
        font-size: 12px;
        color: #696e79;
    }
    .popper__arrow {
        display: none!important;
    }
    .el-dropdown-menu.el-popper {
        padding: 0px;
        margin-top: 0px;
        border-radius: 0px;
        margin-left: 5px;
        width: 80px;
        color: #696e79;
    }
    .el-dropdown-menu.el-popper .el-dropdown-menu__item {
        font-size: 12px;
        padding: 0;
        text-align: center;
    }
    .el-dropdown-menu.el-popper .el-dropdown-menu__item:hover{
        color: #0c92fe;
        background-color: #f5f7f8;
    }
    .el-dropdown-menu.el-popper .el-dropdown-menu__item a {
        display: inline-block;
        width: 100%;
    }
    .netdisk-list-operation {
        color: #22a5df;
    }
    .netdisk-list-operation i{
        margin-left: 5px;
    }
    .copy-dialog .el-dialog{
        width: 600px;
    }
    .netdisk-link {
        color: #22a5df;
        cursor: pointer;
    }
    .copy-dialog .selected-files {
        margin-left: 25px;
        color: #696e79;
    }
    .copy-dialog .selected-area {
        margin-top: 10px;
        width: 500px;
        height: 0px;
        background-color: #f5f7f8;
        animation: delayHidden 0.2s forwards;
        padding: 0px 10px;
    }
    .copy-dialog .selected-area.is-active {
        animation: delayShow 0.2s forwards;
        padding: 13px 10px;
    }
    .copy-dialog .folder-list {
        width: 500px;
        height: 180px;
        background-color: #ffffff;
        border-radius: 1px;
        border: solid 1px #cad8df;
        overflow-y: auto;
    }
    .share-dialog .el-dialog {
        width: 790px;
    }
    .share-select {
        width: 320px;
        height: 400px;
        background-color: #ffffff;
        border: solid 1px #cad8df;
        overflow-y: auto;
    }
    .share-middle {
        width: 70px;
        height: 400px;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
    }
    .share-middle > span {
        display: inline-block;
        width: 30px;
        height: 30px;
        background-color: #e6f4ff;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        color: #22a5df;
        font-size: 20px;
        cursor: pointer;
    }
    .share-dialog .el-checkbox__input.is-checked+.el-checkbox__label {
        color: #696e79;
    }
    .info-dialog .el-dialog {
        width: 980px;
    }
    .share-info-body .gutter {
        background: #f5f7f8;
    }
    .share-info-body .el-table::before {
        height: 0;
    }
    .waring-tip-dialog .el-dialog {
        width: 470px;
        color: #696e79;
    }
    .waring-tip-dialog .pull-left {
        color: #ec670b;
        font-size: 40px;
        margin-left: 40px;
        margin-right: 20px;
    }
    .waring-tip-dialog .pull-left + div {
        padding-right: 85px;
    }
    .waring-tip-dialog .text-info {
        color: #0c92fe;
    }
    .netdisk-list-folder {
        display: inline-block;
        width: 100%;
    }
    .netdisk-list-folder:hover {
        color: #22a5df;
    }
    @keyframes delayHidden {
        from {
                height: 80px;
                border: solid 1px #cad8df;
            } to {
                height: 0px;
                border: none;
            }
    }
    @keyframes delayShow {
        from {
                height: 0px;
                border: none;
            } to {
                height: 80px;
                border: solid 1px #cad8df;
            }
    }
</style>


