<template>
    <div>
        <el-table
            stripe
            :data="tableData" 
            :header-cell-style="headerRowStyle"
            @selection-change="selectChange">
            <el-table-column type="selection"></el-table-column>
            <el-table-column key="name" :label="$t('table.hwName')">
                <template slot-scope="scope">
                    <a class="netdisk-list-folder" @click="getChildrenFiles(scope.row)" v-if="scope.row.type === 0">{{scope.row.name}}</a>
                    <span v-else>{{scope.row.name}}</span>
                </template>
            </el-table-column>
            <el-table-column key="operation" width="200">
                <template slot-scope="scope">
                    <div class="netdisk-list-operation">
                        <a v-for="operator in operators" @click="operator.handler(scope.row)">
                            <span :title="operator.showName"><i :class="operator.iconClass"></i></span>
                            <span class="not-pointer" v-if="operator.name === 'share' && scope.row.shares && scope.row.shares.length > 0" :title="$t('table.shared', {value: scope.row.shares.length})">
                                {{$t('table.shared', {value: scope.row.shares.length})}}
                            </span>
                        </a>
                    </div>
                </template>
            </el-table-column>
            <el-table-column v-for="item in fields" :key="item.name" :label="item.showName" :width="item.width">    
                <template slot-scope="scope">
                    <span>{{scope.row[item.name]}}</span>
                </template>
            </el-table-column>
        </el-table><br>
        <span class="netdisk-table-footer" v-if="footerData">
            {{$t('table.footer.already')}}
            <span>{{footerData.already}}</span>
            {{$t('table.footer.item')}}&nbsp;&nbsp;
            {{$t('table.footer.all')}}
            <span>{{footerData.all}}</span>
            {{$t('table.footer.item')}}
        </span>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                newFolderList: []
            }
        },
        props: {
            tableData: {
                type: Array,
                default: () => []
            },
            fields: {
                type: Array,
                default: () => []
            },
            headerRowStyle: {
                type: Object,
                default: () => {
                    return {backgroundColor: '#f5f7f8'}
                }
            },
            footerData: {
                type: Object,
                default: () => {
                    return {}
                }
            },
            operators: {
                type: Array,
                default: () => []
            }
        },
        methods: {
            selectChange(selectRows) {
                this.$emit('selection-change', selectRows)
            },
            getChildrenFiles(item) {
                this.$emit('getChildrenFiles', item)
            }
        }
    }
</script>
<i18n>
    {
        "zh": {
            "table": {
                "footer": {
                    "already": "已加载",
                    "all": "共",
                    "item": "项"
                },
                "hwName": "作业名称",
                "shared": "已分享({value}人)"
            }
        }
    }
</i18n>
<style scoped>
    a {
        cursor: pointer;
    }
    a:focus {
        border: 0;
    }
    a:-webkit-any-link {
        color: #0c92fe;
    }
    .netdisk-table-footer {
        font-size: 12px;
        color: #696e79;
    }
    .netdisk-table-footer > span {
        display: inline-block;
        padding: 3px;
        color: #0c92fe;
    }
    .not-pointer {
        cursor: default;
    }
</style>


