<template>
    <div>
        <div v-for="item in activeData" v-if="item.type === 0">
            <div :class="['folder-list-column', item.isSelect?'is-selected':'']" @click="getSelectedFolder(item)">
                <span :class="[item.shares?'':'no-child-folder']" v-if="hasChild"></span>
                <i class="fa fa-caret-down folder-icon" v-if="item.shares && item.isOpen"></i>
                <i class="fa fa-caret-right folder-icon" v-if="item.shares && !item.isOpen"></i>
                <i class="fa fa-folder text-folder"></i>
                <span class="folder-name">{{item.name}}</span>
            </div>

            <div class="folder-list-body">
                <folder-list 
                    :data="item.folder" 
                    @createFolder="createFolder"
                    @getSelectedFolder="getSelectedFolder" 
                    v-if="item.folder && item.type === 'folder'"></folder-list>
                <div class="folder-list-column" v-for="(newFile,index) in item.newFolders" v-if="item.newFolders.length!==0">
                    <span>
                        <i class="fa fa-folder text-folder"></i>
                        <input autofocus 
                            type="text"
                            class="edit-folder" 
                            v-model="newFile.value"
                            onfocus="this.select()"
                            @keyup="createFolder($event, 'key')"
                            @blur="createFolder($event, 'blur')"
                            ><br>
                    </span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import folderList from './folderList'
    export default {
        data() {
            return {
                activeData: this.data
            }
        },
        props: {
            data: {
                type: Array,
                default: () => []
            }
        },
        components: {
            folderList
        },
        name: 'folder-list',
        computed: {
            hasChild() {
                let hasChild = false
                this.data.some((item) => {
                    if(item.shares) {
                        hasChild = true
                    }
                })
                return hasChild
            }
        },
        methods: {
            getSelectedFolder(obj) {
                obj.isOpen = !obj.isOpen
                this.$emit('getSelectedFolder', obj.path)
            },
            createFolder(event, type, name) {
                if(type === 'key' && event.keyCode !==13){
                    return
                }
                this.$emit('createFolder')
            }
        },
        watch: {
            data(val) {
                this.activeData = val
            }
        }
    }
</script>
<style scoped>
    .folder-list-column {
        min-height: 30px;
        line-height: 30px;
        padding-left: 10px;
        border-radius: 1px;
        color: #696e79;
        font-size: 14px;
        cursor: pointer;
    }
    .folder-list-column.is-active {
        background-color: rgba(69, 154, 222, 0.08);
    }
    .text-folder {
        color: #d5c3b2;
    }
    .folder-list-body {
        padding-left: 33px;
    }
    .folder-icon {
        width: 13px;
        text-align: right;
    }
    .no-child-folder {
        margin-left: 17px !important;
    }
    .edit-folder {
        width: 120px;
        height: 18px;
        line-height: 18px;
        padding-left: 5px;
        background-color: #ffffff;
        border-radius: 1px;
        border: solid 1px #0c92fe;
        display: inline-block;
    }
    .is-selected .text-folder, .is-selected .folder-name {
        color: #22a5df;
    }
    </style>
    <i18n>
        {
            "zh": {
                "newFolder": "新建文件夹"
            }
        }
    </i18n>


